.card {
  border-radius: 16px;
  background: var(--background-secondary);
  box-shadow: 0px 3px 8px -1px rgba(50, 50, 71, 0.05), 0px 0px 1px 0px rgba(12, 26, 75, 0.24);
  padding: 20px;
  margin-bottom: 20px;
}

.wp-block-button .wp-block-button__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 16px 24px;
  border-radius: 8px !important;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 112.5%;
  transition: 0.3s all ease;
}
.wp-block-button .wp-block-button__link:not(.has-background), .wp-block-button .wp-block-button__link:not(.has-text-color) {
  background: var(--accent-1) !important;
  border: 1px solid var(--accent-1) !important;
  color: var(--background-secondary) !important;
}
.wp-block-button .wp-block-button__link:not(.has-background):hover, .wp-block-button .wp-block-button__link:not(.has-background):focus, .wp-block-button .wp-block-button__link:not(.has-text-color):hover, .wp-block-button .wp-block-button__link:not(.has-text-color):focus {
  background: var(--accent-1-dark) !important;
  border: 1px solid var(--accent-1-dark) !important;
  color: var(--background-secondary) !important;
}
.wp-block-button.is-style-outline .wp-block-button__link:not(.has-background), .wp-block-button.is-style-outline .wp-block-button__link:not(.has-text-color) {
  background: transparent !important;
  border: 1px solid var(--accent-1) !important;
  color: var(--accent-1) !important;
}
.wp-block-button.is-style-outline .wp-block-button__link:not(.has-background):hover, .wp-block-button.is-style-outline .wp-block-button__link:not(.has-background):focus, .wp-block-button.is-style-outline .wp-block-button__link:not(.has-text-color):hover, .wp-block-button.is-style-outline .wp-block-button__link:not(.has-text-color):focus {
  background: var(--accent-1) !important;
  color: var(--background-secondary) !important;
}

button {
  border-radius: 8px;
  padding: 10px 30px;
}
button.outline {
  background: transparent !important;
  border: 1px solid var(--accent-1) !important;
  color: var(--accent-1) !important;
}
button.outline:hover, button.outline:focus {
  background: var(--accent-1) !important;
  color: var(--background-secondary) !important;
}